import React, { Component } from "react";
import addUser from "../Images/AddUser2.svg";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider, {
  Search,
  CSVExport,
} from "react-bootstrap-table2-toolkit";
import paginationFactory from "react-bootstrap-table2-paginator";
import FormAddUser from "./Component/FormAddUser";
import { getDatauser, getUserData } from "../actions/userAction";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import Axios from "axios";
import { berhasil, gagal } from "../Alert";
import { getUUID } from "../actions/fakturAction";
import daftarArray from "../DraftMenu.json";

const { SearchBar } = Search;
const { ExportCSVButton } = CSVExport;

const maptostate = (state) => {
  return {
    listDataUser: state.provinsi.listDataUser,
  };
};
const server = process.env.REACT_APP_BACKEND_URL;
const server_SET_MENU = process.env.REACT_APP_BACKEND_URL_SET_MENU;
class AddUser extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: [
        {
          dataField: "userId",
          text: "ID User",
        },
        {
          dataField: "userName",
          text: "Nama Pegawai",
          headerStyle: () => {
            return { width: "20%" };
          },
        },
        {
          dataField: "userLevel",
          text: "Level",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-warning mr-2"
                  data-target="#modalTambahUser"
                  data-toggle="modal"
                  onClick={() =>
                    this.props.dispatch(
                      getUserData(
                        row.idUser,
                        row.namaPegawai,
                        row.level,
                        row.password
                      )
                    )
                  }
                >
                  <i className="fa fa-edit mr-1"></i>
                  Edit
                </button>
                <button
                  className="btn btn-danger mr-2"
                  onClick={() => alert("NOT FUNCTION")}
                >
                  <i className="fa fa-trash mr-1"></i>
                  HAPUS
                </button>
              </div>
            );
          },
          headerStyle: () => {
            return { width: "20%" };
          },
        },
      ],
      data: [
        {
          idUser: "OCT",
          namaPegawai: "OCTAVIAN",
          level: "ADMIN",
          password: "123456",
          rePassword: "123456",
        },
      ],
    };
  }

  componentDidMount() {
    this.props.dispatch(getDatauser());
  }

  handleSubmit(data) {
    var simpan = {
      footer: {
        inputBy: this.props.noFaktur,
      },
      userName: data.username,
      password: data.password,
      statusAktif: "AKTIF",
      userId: data.userId,
      userLevel: data.level,
    };
    var simpanMenu = {
      userId: data.userId,
      daftarMenu: daftarArray,
    };
    Axios.post(server + "user", simpan)
      .then(() =>
        berhasil(
          "User berhasil disimpan",
          this.props.dispatch(getUUID(), this.props.dispatch(getDatauser()))
        )
      )
      .then(() =>
        Axios.post(
          server_SET_MENU + "User/Authorization",
          simpanMenu
        ).catch((err) => console.log(err))
      )
      .catch(() =>
        gagal("Sepertinya ada gangguan, mohon periksa koneksi anda")
      );
  }
  render() {
    return (
      <div className="container-fluid">
        <ol className="breadcrumb mb-2">
          <Link to="/ayugold.com/manageUser">
            <button type="button" className="btn btn-secondary">
              <i className="fa fa-angle-left mr-3" aria-hidden="true"></i> Back
            </button>
          </Link>
        </ol>
        <div className="card">
          <div className="card-body">
            <div className="row">
              <div className="col-lg-12">
                <div className="text-center">
                  <img src={addUser} alt="Add User Logo" width={200} />
                  <p>
                    Silahkan Simpan ID dan password baik baik, jangan password
                    <br />
                    dengan kata umum, seperti: Tanggal lahir, atau nama
                  </p>
                </div>
              </div>
              <div className="col-lg-12">
                <div className="text-right">
                  <button
                    className="btn btn-primary"
                    data-target="#modalTambahUser"
                    data-toggle="modal"
                  >
                    Tambah User
                  </button>
                </div>
              </div>
              <div className="col-lg-12">
                {this.props.listDataUser ? (
                  <ToolkitProvider
                    keyField="id"
                    data={this.props.listDataUser}
                    columns={this.state.columns}
                    search
                  >
                    {(props) => (
                      <div className="form-group">
                        <div className="text-right mt-5 mb-3">
                          <SearchBar
                            {...props.searchProps}
                            className="form-control"
                            placeholder="Tuliskan Kode / Nama"
                            style={{ margin: 0, top: "50%" }}
                          />
                        </div>
                        <BootstrapTable
                          {...props.baseProps}
                          striped
                          hover
                          condensed
                          bordered={true}
                          headerClasses="header-table"
                          pagination={paginationFactory()}
                        />
                        <ExportCSVButton
                          {...props.csvProps}
                          className="btn btn-primary"
                        >
                          Export CSV
                        </ExportCSVButton>
                      </div>
                    )}
                  </ToolkitProvider>
                ) : null}
              </div>
            </div>
          </div>
        </div>
        <div
          id="modalTambahUser"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Data Area
                </h4>
                <button
                  id="dismiss_modal"
                  name="dismiss_modal"
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  Ã—
                </button>
              </div>
              <div className="modal-body">
                <FormAddUser onSubmit={(data) => this.handleSubmit(data)} />
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptostate, null)(AddUser);
