import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import validasiUser from "../../Validations/validasiUser";

const renderField = ({
  input,
  label,
  type,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);
const maptoState = (state) => {
  return {
    initialValues: {
      namaLengkap: state.provinsi.editUser.namaUser,
      userID: state.provinsi.editUser.idUser,
      password: state.provinsi.editUser.password,
      rePassword: state.provinsi.editUser.password,
      level: state.provinsi.editUser.level,
    },
  };
};
class ForAddUser extends Component {
  constructor(props) {
    super(props);
    this.state = {
      response: "",
      status: "AKTIF",
      listProvinsi: [],
    };
  }

  render() {
    return (
      <div>
        <form onSubmit={this.props.handleSubmit}>
          <div className="row">
            <div className="col-lg-4">
              <Field
                name="username"
                type="text"
                normalize={(value) => value && value.toUpperCase()}
                component={renderField}
                label="Nama Lengkap"
              />
            </div>
            <div className="col-lg-4">
              <Field
                name="userId"
                type="text"
                normalize={(value) => value && value.toUpperCase()}
                component={renderField}
                label="User ID"
              />
            </div>
            <div className="col-lg-4">
              <label htmlFor="">Level</label>
              <Field name="level" component="select" className="form-control">
                <option value="ADMIN" key="ADMIN">
                  ADMIN
                </option>
                <option value="MARKETING" key="MARKETING">
                  MARKETIN
                </option>
                <option value="OWNER" key="OWNER">
                  OWNER
                </option>
                <option value="SUPERVISOR" key="SUPERVISOR">
                  SUPERVISOR
                </option>
              </Field>
            </div>
            <div className="col-lg-4">
              <Field
                name="password"
                type="password"
                normalize={(value) => value && value.toUpperCase()}
                component={renderField}
                label="Password"
              />
            </div>
            <div className="col-lg-4">
              <Field
                name="rePassword"
                type="password"
                normalize={(value) => value && value.toUpperCase()}
                component={renderField}
                label="Retype Password"
              />
            </div>
            <div className="col-lg-12 col-md-12">
              <div className="text-right">
                <button
                  type="submit"
                  id="simpan"
                  className="btn btn-primary"
                  disabled={this.props.submitting}
                >
                  Submit
                </button>
              </div>
            </div>
          </div>
        </form>
      </div>
    );
  }
}

ForAddUser = reduxForm({
  form: "AddUser", // a unique identifier for this form
  enableReinitialize: true,
  validate: validasiUser,
})(ForAddUser);
export default connect(maptoState, null)(ForAddUser);
